import React, { useEffect, useState } from 'react';
import { Navbar, Nav, NavDropdown, Container } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom'
import logo from 'assets/logo.png'; //  logo 
import Axios from 'common/Axios'
import "css/Navlogo.css"

/**
 * 
 * @returns html
 */
function ManagerNav() {
    const [userInfo,setUserInfo] = useState(null);
    const navigator = useNavigate()

    useEffect(() => {
        // token
        if (window.localStorage.getItem("IsAuth") === false){
            Axios().post("member/token/verify/",{
                'token':window.localStorage.getItem("jwt")
            })
            .catch((err) => {
                if (err.response.status === 401){
                    alert("")
                    navigator('/login')
                }
            })
        }
    } , [])


    return (
        <Container fluid >
            <Navbar bg="light" expand="lg">
                <Navbar.Brand href="/" className="d-flex align-items-center mx-5 ">
                    <img
                    src={logo}
                    className="d-inline-block align-top logo-img"
                    alt="LOGO"
                    />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav" className='justify-content-end' >
                    <Nav className="ml-auto">
                        <Nav.Link href="/alumni/manage/member/" className="mx-3 border-item"></Nav.Link>
                        <Nav.Link href="/alumni/manage/company/" className="mx-3 border-item"></Nav.Link>
                        <Nav.Link href="/alumni/manage/product/" className="mx-3 border-item"></Nav.Link>
                        <Nav.Link href="/alumni/manage/recruit/" className="mx-3 border-item"></Nav.Link>
                        <Nav.Link href="/alumni/manage/pic/" className="mx-3 border-item"></Nav.Link>
                        <NavDropdown title="" id="basic-nav-dropdown" className='mr-auto border-item'>
                            <NavDropdown.Item href="/login"></NavDropdown.Item>
                            <NavDropdown.Item href="/Manager/User"></NavDropdown.Item>
                        </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </Container>

    );
}

export default ManagerNav;