import React from 'react';
import { Form, Button, Container, Row, Col } from 'react-bootstrap';

function UserFilter({ filters, setFilters, applyFilters, handleAddUser , handleAddAccount}) {
  // 
  const handleFilterChange = (e) => {
    setFilters({
      ...filters,
      [e.target.name]: e.target.value,
    });
  };

  return (
    <Container>
      <h5></h5>
      <Form>
        <Form.Group controlId="filterName">
          <Form.Label></Form.Label>
          <Form.Control
            type="text"
            name="name"
            value={filters.name}
            onChange={handleFilterChange}
          />
        </Form.Group>

        {/*  */}

        <Form.Group controlId="filterPosition">
          <Form.Label></Form.Label>
          <Form.Control
            type="text"
            name="position"
            value={filters.position}
            onChange={handleFilterChange}
          />
        </Form.Group>

        <Form.Group controlId="filterIsPaid">
          <Form.Label></Form.Label>
          <Form.Control
            as="select"
            name="is_paid"
            value={filters.is_paid}
            onChange={handleFilterChange}
          >
            <option value=""></option>
            <option value="true"></option>
            <option value="false"></option>
          </Form.Control>
        </Form.Group>

        <Button variant="primary" onClick={applyFilters} className="mt-2">
          
        </Button>
      </Form>

      <hr />

      <h5></h5>
      <Row>
        <Col>
            <Button variant="success" onClick={handleAddUser}>
                
            </Button>
        </Col>
        <Col>
            <Button variant="success" onClick={handleAddUser}>
                
            </Button>
        </Col>
      </Row>



    </Container>
  );
}

export default UserFilter;
