import React, { useState } from 'react';
import { Modal, Button, Form, Table, Container } from 'react-bootstrap';


/**
 * 
 */
function RecruitManaPage() {
  const [jobs, setJobs] = useState([
    {
      id: 1,
      title: '',
      company: 'XYZ ',
      postDate: '2023-09-01',
      endDate: '2023-09-30',
      contactName: '',
      contactEmail: 'zhangsan@example.com',
      description: ' Django ',
    },
    {
      id: 2,
      title: '',
      company: 'ABC ',
      postDate: '2023-08-15',
      endDate: '2023-09-15',
      contactName: '',
      contactEmail: 'lisi@example.com',
      description: ' React  JavaScript',
    },
  ]);

  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [formData, setFormData] = useState({
    id: '',
    title: '',
    company: '',
    postDate: '',
    endDate: '',
    contactName: '',
    contactEmail: '',
    description: '',
  });

  const [isPersonalContact, setIsPersonalContact] = useState(false);
  const [isPersonalCompany, setIsPersonalCompany] = useState(false);

  // Toggle Modals
  const handleCloseAddModal = () => setShowAddModal(false);
  const handleShowAddModal = () => setShowAddModal(true);
  const handleCloseEditModal = () => setShowEditModal(false);

  // Handle Form Data Changes
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  // Handle Add Job
  const handleAddJob = (e) => {
    e.preventDefault();
    const newJob = {
      ...formData,
      id: jobs.length + 1,
      contactName: isPersonalContact ? '' : formData.contactName,
      company: isPersonalCompany ? '' : formData.company,
    };
    setJobs([...jobs, newJob]);
    setFormData({
      id: '',
      title: '',
      company: '',
      postDate: '',
      endDate: '',
      contactName: '',
      contactEmail: '',
      description: '',
    });
    setIsPersonalContact(false);
    setIsPersonalCompany(false);
    setShowAddModal(false);
  };

  // Handle Edit Job
  const handleEditJob = (id) => {
    const job = jobs.find((job) => job.id === id);
    setFormData(job);
    setShowEditModal(true);
  };

  const handleSaveEditJob = (e) => {
    e.preventDefault();
    setJobs(
      jobs.map((job) => (job.id === formData.id ? { ...formData } : job))
    );
    setShowEditModal(false);
  };

  // Handle Delete Job
  const handleDeleteJob = (id) => {
    setJobs(jobs.filter((job) => job.id !== id));
  };

  return (
    <Container className="mt-5">
      <h1 className="mb-4"></h1>
      <Button variant="success" onClick={handleShowAddModal}>
        
      </Button>

      <Table hover bordered className="mt-4">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {jobs.map((job) => (
            <tr key={job.id}>
              <td>{job.id}</td>
              <td>{job.title}</td>
              <td>{job.company}</td>
              <td>{job.postDate}</td>
              <td>{job.endDate}</td>
              <td>
                <Button
                  variant="warning"
                  className="me-2"
                  onClick={() => handleEditJob(job.id)}
                >
                  
                </Button>
                <Button variant="danger" onClick={() => handleDeleteJob(job.id)}>
                  
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>

      {/* Add Job Modal */}
      <Modal show={showAddModal} onHide={handleCloseAddModal}>
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={handleAddJob}>
            <Form.Group className="mb-3" controlId="formJobDescription">
              <Form.Label></Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                name="description"
                value={formData.description}
                onChange={handleInputChange}
                required
              />
            </Form.Group>
            <Form.Check
              type="checkbox"
              id="isPersonalContact"
              label=""
              checked={isPersonalContact}
              onChange={() => setIsPersonalContact(!isPersonalContact)}
            />
            <Form.Check
              type="checkbox"
              id="isPersonalCompany"
              label=""
              checked={isPersonalCompany}
              onChange={() => setIsPersonalCompany(!isPersonalCompany)}
            />

            <Form.Group className="mb-3" controlId="formJobCompany">
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                name="company"
                value={formData.company}
                onChange={handleInputChange}
                required
                disabled={isPersonalCompany}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formPostDate">
              <Form.Label></Form.Label>
              <Form.Control
                type="date"
                name="postDate"
                value={formData.postDate}
                onChange={handleInputChange}
                required
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formEndDate">
              <Form.Label></Form.Label>
              <Form.Control
                type="date"
                name="endDate"
                value={formData.endDate}
                onChange={handleInputChange}
                required
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formContactName">
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                name="contactName"
                value={formData.contactName}
                onChange={handleInputChange}
                required
                disabled={isPersonalContact}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formContactEmail">
              <Form.Label> Email</Form.Label>
              <Form.Control
                type="email"
                name="contactEmail"
                value={formData.contactEmail}
                onChange={handleInputChange}
                required
                disabled={isPersonalContact}
              />
            </Form.Group>
            <Button variant="primary" type="submit">
              
            </Button>
          </Form>
        </Modal.Body>
      </Modal>

      {/* Edit Job Modal */}
      <Modal show={showEditModal} onHide={handleCloseEditModal}>
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={handleSaveEditJob}>
            <Form.Group className="mb-3" controlId="formJobDescription">
              <Form.Label></Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                name="description"
                value={formData.description}
                onChange={handleInputChange}
                required
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formJobCompany">
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                name="company"
                value={formData.company}
                onChange={handleInputChange}
                required
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formPostDate">
              <Form.Label></Form.Label>
              <Form.Control
                type="date"
                name="postDate"
                value={formData.postDate}
                onChange={handleInputChange}
                required
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formEndDate">
              <Form.Label></Form.Label>
              <Form.Control
                type="date"
                name="endDate"
                value={formData.endDate}
                onChange={handleInputChange}
                required
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formContactName">
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                name="contactName"
                value={formData.contactName}
                onChange={handleInputChange}
                required
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formContactEmail">
              <Form.Label> Email</Form.Label>
              <Form.Control
                type="email"
                name="contactEmail"
                value={formData.contactEmail}
                onChange={handleInputChange}
                required
              />
            </Form.Group>
            <Button variant="primary" type="submit">
              
            </Button>
          </Form>
        </Modal.Body>
      </Modal>
    </Container>
  );
}

export default RecruitManaPage;
