import React, { useState, useEffect } from 'react';
import { Table, Button, Modal, Container ,Row,Col} from 'react-bootstrap';
import axios from 'axios';
import ProductFormModal from 'components/Manage/ProductManage/ProductFormModal';
import SearchBar from 'components/Manage/ProductManage/SearchBar';

function ProductList() {
    // 
    const fakeProducts = [
        { id: 1, name: 'A', description: 'A', photo: null },
        { id: 2, name: 'B', description: 'B', photo: null },
        { id: 3, name: 'C', description: 'C', photo: null },
    ];

    const [products, setProducts] = useState(fakeProducts);
    const [filteredProducts, setFilteredProducts] = useState(fakeProducts);
    const [showModal, setShowModal] = useState(false);
    const [selectedProduct, setSelectedProduct] = useState(null);

    const handleSearch = (query) => {
        if (!query) {
            setFilteredProducts(products);
        } else {
            const filtered = products.filter((product) =>
                product.name.toLowerCase().includes(query.toLowerCase())
            );
            setFilteredProducts(filtered);
        }
    };

    const handleEdit = (product) => {
        setSelectedProduct(product);
        setShowModal(true);
    };

    const handleAdd = () => {
        setSelectedProduct(null);
        setShowModal(true);
    };

    const handleCloseModal = (newProduct) => {
        setShowModal(false);

        // 
        if (newProduct) {
            if (selectedProduct) {
                // 
                setProducts((prev) =>
                    prev.map((p) => (p.id === selectedProduct.id ? newProduct : p))
                );
            } else {
                // 
                setProducts((prev) => [...prev, { ...newProduct, id: prev.length + 1 }]);
            }
            setFilteredProducts(products);
        }
    };

    return (
        <Container className='my-5'>
            <h2></h2>
            <Row>
                <Col>
                <SearchBar onSearch={handleSearch} />
                </Col>
                <Col>
                <Button onClick={handleAdd} className="mb-3"></Button>
                </Col>
            </Row>
            
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th> {/*  */}
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {filteredProducts.map((product) => (
                        <tr key={product.id}>
                            <td>{product.name}</td>
                            <td>{product.description}</td>
                            <td>
                                {product.photo ? (
                                    <img
                                        src={product.photo}
                                        alt={product.name}
                                        style={{ width: '100px', height: '100px', objectFit: 'cover' }}
                                    />
                                ) : (
                                    <span></span>
                                )}
                            </td>
                            <td>
                                <Button variant="primary" onClick={() => handleEdit(product)}></Button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </Table>

            {showModal && (
                <ProductFormModal
                    product={selectedProduct}
                    show={showModal}
                    handleClose={handleCloseModal}
                />
            )}
        </Container>    );
}

export default ProductList;