import React, { useState } from 'react';
import { Container, Row, Col, Card, Tabs, Tab, Form } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'css/AlumniListPage.css'; //  CSS

// 
const alumniData = [
    { id: 1, name: '', role: 'AI ', year: 100, photo: 'https://via.placeholder.com/100' },
    { id: 2, name: '', role: '', year: 101, photo: 'https://via.placeholder.com/100' },
    { id: 3, name: '', role: '', year: 102, photo: 'https://via.placeholder.com/100' },
    { id: 4, name: '', role: '', year: 103, photo: 'https://via.placeholder.com/100' },
    { id: 5, name: '', role: '', year: 104, photo: 'https://via.placeholder.com/100' },
    { id: 6, name: '', role: '', year: 105, photo: 'https://via.placeholder.com/100' },
    { id: 7, name: '', role: '', year: 106, photo: 'https://via.placeholder.com/100' },
    // ...
];

// 
const yearsOrder = ['', 100, 101, 102, 103, 104, 105, 106, 107, 108, 109, 110, 111, 112, 113, 114];

// 
const filterAlumniBySearch = (alumniList, searchQuery) => {
    if (!searchQuery) return alumniList;
    return alumniList.filter((alumni) =>
        alumni.name.toLowerCase().includes(searchQuery.toLowerCase())
    );
};

// 
const filterAlumniByYear = (alumniList, selectedYear) => {
    if (selectedYear === '') return alumniList; //  ""
    return alumniList.filter((alumni) => alumni.year === parseInt(selectedYear)); //  year 
};

const AlumniListPage = () => {
    const [key, setKey] = useState(''); //  ""
    const [searchQuery, setSearchQuery] = useState(''); // 

    // 
    const filteredAlumni = filterAlumniBySearch(
        filterAlumniByYear(alumniData, key),
        searchQuery
    );

    return (
        <Container>
            <h1 className="text-center my-4"></h1>

            {/*  */}
            <Form className="mb-4">
                <Form.Control
                    type="text"
                    placeholder="..."
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                />
            </Form>

            {/*  Tabs  */}
            <Tabs
                id="alumni-year-tabs"
                activeKey={key}
                onSelect={(k) => setKey(k)}
                className="mb-4"
            >
                {yearsOrder.map((year) => (
                    <Tab eventKey={year.toString()} title={year === '' ? '' : `${year}`} key={year}>
                        <Row>
                            {filteredAlumni.map((alumni) => (
                                <Col xs={12} sm={6} md={4} lg={2} className="mb-4 fade-in" key={alumni.id}>
                                    <Card
                                        className="h-100 shadow"
                                        onClick={() => window.location.href = `/alumni/${alumni.id}`} // 
                                        style={{ cursor: 'pointer' }} // 
                                    >
                                        <Card.Img variant="top" src={alumni.photo} alt={alumni.name} />
                                        <Card.Body>
                                            <Card.Title>{alumni.name}</Card.Title>
                                            <Card.Text>{alumni.role}</Card.Text>
                                        </Card.Body>
                                    </Card>
                                </Col>
                            ))}
                        </Row>
                    </Tab>
                ))}
            </Tabs>
        </Container>
    );
};

export default AlumniListPage;
