import React, { useState, useEffect } from 'react';
import { Modal, Button, Form, Col, Row } from 'react-bootstrap';
import axios from 'axios'; // Axios 

const MemberModal = ({ show, handleClose, handleSave }) => {
  const [formData, setFormData] = useState({
    name: '',
    home_phone: '',
    mobile_phone: '',
    gender: '',
    address: '',
    intro: '',
    birth_date: '',
    photo: null,
    position: '',
    graduate: '',
  });

//   useEffect(() => {
//     if (memberId) {
//       //  axios 
//       //  API: `/api/members/${memberId}`
//       axios
//         .get(`/api/members/${memberId}`)
//         .then((response) => {
//           setFormData(response.data);
//         })
//         .catch((error) => {
//           console.error("Error fetching member data", error);
//         });
//     }
//   }, [memberId]);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData({
      ...formData,
      [name]: type === 'checkbox' ? checked : value,
    });
  };

  const handleFileChange = (e) => {
    setFormData({
      ...formData,
      photo: e.target.files[0],
    });
  };

  const handleSubmit = () => {
    handleSave(formData);
  };

  return (
    <Modal show={show} onHide={handleClose} size="lg" centered>
      <Modal.Header closeButton>
        <Modal.Title></Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Row>
            <Col xs={12} md={6}>
              <Form.Group controlId="name">
                <Form.Label></Form.Label>
                <Form.Control
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
            <Col xs={12} md={6}>
              <Form.Group controlId="home_phone">
                <Form.Label></Form.Label>
                <Form.Control
                  type="text"
                  name="home_phone"
                  value={formData.home_phone}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs={12} md={6}>
              <Form.Group controlId="mobile_phone">
                <Form.Label></Form.Label>
                <Form.Control
                  type="text"
                  name="mobile_phone"
                  value={formData.mobile_phone}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
            <Col xs={12} md={6}>
              <Form.Group controlId="gender">
                <Form.Label></Form.Label>
                <Form.Control
                  as="select"
                  name="gender"
                  value={formData.gender}
                  onChange={handleChange}
                >
                  <option value="M"></option>
                  <option value="F"></option>
                  <option value="O"></option>
                </Form.Control>
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs={12} md={6}>
              <Form.Group controlId="birth_date">
                <Form.Label></Form.Label>
                <Form.Control
                  type="date"
                  name="birth_date"
                  value={formData.birth_date}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
            <Col xs={12} md={6}>
              <Form.Group controlId="address">
                <Form.Label></Form.Label>
                <Form.Control
                  as="textarea"
                  rows={2}
                  name="address"
                  value={formData.address}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs={12} md={6}>
              <Form.Group controlId="graduate_year">
                <Form.Label></Form.Label>
                <Form.Control
                  type="text"
                  name="graduate_year"
                  value={formData.position}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
            <Col xs={12} md={6}>
              <Form.Group controlId="graduate">
                <Form.Label></Form.Label>
                <Form.Control
                  type="text"
                  name="graduate"
                  value={formData.graduate}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs={12} md={6}>
              <Form.Group controlId="photo">
                <Form.Label></Form.Label>
                <Form.Control
                  type="file"
                  name="photo"
                  onChange={handleFileChange}
                />
              </Form.Group>
            </Col>
          </Row>

          <Form.Group controlId="intro">
            <Form.Label></Form.Label>
            <Form.Control
              as="textarea"
              rows={3}
              name="intro"
              value={formData.intro}
              onChange={handleChange}
            />
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          
        </Button>
        <Button variant="primary" onClick={handleSubmit}>
          
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default MemberModal;
