import React, { useState } from 'react';
import { Table, Modal, Button, Container } from 'react-bootstrap';
import 'css/recruit.css';

const jobs = [
  {
    id: 1,
    title: '',
    postDate: '2023-09-01',
    endDate: '2023-09-30',
    company: 'XYZ ',
    description: ' Django  REST API ',
    contactName: '',
    contactEmail: 'zhangsan@example.com',
  },
  {
    id: 2,
    title: '',
    postDate: '2023-08-15',
    endDate: '2023-09-15',
    company: 'ABC ',
    description: ' React ',
    contactName: '',
    contactEmail: 'lisi@example.com',
  },
  {
    id: 3,
    title: 'AI',
    postDate: '2023-08-25',
    endDate: '2023-09-25',
    company: 'ML ',
    description: ' TensorFlow  PyTorch',
    contactName: '',
    contactEmail: 'wangwu@example.com',
  },
  // ...
];

function RecruitPage() {
  const [show, setShow] = useState(false);
  const [selectedJob, setSelectedJob] = useState({});

  const handleClose = () => setShow(false);
  const handleShow = (job) => {
    setSelectedJob(job);
    setShow(true);
  };

  return (
    <Container className="mt-5">
      <h1 className="mb-4"></h1>
      <Table responsive hover className="job-table">
        <thead className="table-dark">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {jobs.map((job, index) => (
            <tr key={job.id} onClick={() => handleShow(job)} className="job-row">
              <td>{index + 1}</td>
              <td>{job.postDate}</td>
              <td>{job.endDate}</td>
              <td>{job.company}</td>
              <td>{job.title}</td>
            </tr>
          ))}
        </tbody>
      </Table>

      {/* Modal for job details */}
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            <strong>:</strong> {selectedJob.title}
          </p>
          <p>
            <strong>:</strong> {selectedJob.postDate}
          </p>
          <p>
            <strong>:</strong> {selectedJob.endDate}
          </p>
          <p>
            <strong>:</strong> {selectedJob.company}
          </p>
          <p>
            <strong>:</strong> {selectedJob.contactName}
          </p>
          <p>
            <strong> Email:</strong> {selectedJob.contactEmail}
          </p>
          <p>
            <strong>:</strong> {selectedJob.description}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
}

export default RecruitPage;
