// UserManagement.js
import React, { useState, useEffect } from 'react';
import { Container, Row, Col } from 'react-bootstrap';

import UserFilter from 'components/Manage/UserManage/UserFilter';
import UserTable from 'components/Manage/UserManage/UserTable';
import EditUserModal from 'components/Manage/UserManage/EditUserModal';

// import axios from 'axios'; // 

function UserManagement() {
  // 
  const [users, setUsers] = useState([]);
  const [filters, setFilters] = useState({
    name: '',
    gender: '',
    school: '',
    position: '',
    is_paid: '',
  });
  const [showModal, setShowModal] = useState(false);
  const [currentUser, setCurrentUser] = useState(null);

  // 
// 
const fakeUsers = [
  {
    id: 1,
    name: '',
    gender: 'M',
    mobile_phone: '0912345678',
    home_phone: '072345678',
    address: '',
    position: { title: '' },
    graduate: { school: '' },
    email: 'zhangsan@gmail.com',
    is_paid: true,
    date_joined: '2020-01-15',
  },
  {
    id: 2,
    name: '',
    gender: 'F',
    mobile_phone: '0922345678',
    home_phone: '073345678',
    address: '',
    position: { title: '' },
    graduate: { school: '' },
    email: 'lisi@gmail.com',
    is_paid: false,
    date_joined: '2021-03-22',
  },
  {
    id: 3,
    name: '',
    gender: 'M',
    mobile_phone: '0932345678',
    home_phone: '074345678',
    address: '',
    position: { title: '' },
    graduate: { school: '' },
    email: 'wangwu@gmail.com',
    is_paid: true,
    date_joined: '2019-06-12',
  },
];

  // 
  useEffect(() => {
    setUsers(fakeUsers);
  }, []);

  //  Modal
  const handleShowModal = (user) => {
    setCurrentUser(user);
    setShowModal(true);
  };

  //  Modal
  const handleCloseModal = () => {
    setShowModal(false);
    setCurrentUser(null);
  };

  // 
  const handleUpdateUser = () => {
    const updatedUsers = users.map((user) =>
      user.id === currentUser.id ? currentUser : user
    );
    setUsers(updatedUsers);
    handleCloseModal();
  };

  // 
  const handleAddUser = () => {
    const newUser = {
      id: users.length + 1,
      name: '',
      position: { title: '' },
      gmail: 'newuser@gmail.com',
      is_paid: false,
    };
    setUsers([...users, newUser]);
  };

  return (
    <Container className='my-5'>
      <Row className="justify-content-center">
        {/*  */}
        <Col md={3} className="bg-light p-3">
          <UserFilter
            filters={filters}
            setFilters={setFilters}
            applyFilters={() => {}}
            handleAddUser={handleAddUser}
          />
        </Col>

        {/*  */}
        <Col md={9} className="p-3">
          <UserTable users={users} handleShowModal={handleShowModal} />
        </Col>
      </Row>

      {/*  Modal */}
      {currentUser && (
        <EditUserModal
          showModal={showModal}
          handleCloseModal={handleCloseModal}
          currentUser={currentUser}
          setCurrentUser={setCurrentUser}
          handleUpdateUser={handleUpdateUser}
        />
      )}
    </Container>
  );
}

export default UserManagement;
