import React, { useState, useEffect } from "react";
import { Container, Carousel } from "react-bootstrap";
import CompanyCard from "components/User/Home/CompanyCard"; // 
import 'css/user/homepage/CompanyList.css'; // 

const CompanyList = () => {
    // 
    const companies = [
        { name: " A", alumni: " A", description: " A", product: " A", imageUrl: "https://via.placeholder.com/150" },
        { name: " B", alumni: " B", description: " B", product: " B", imageUrl: "https://via.placeholder.com/150" },
        { name: " C", alumni: " C", description: " C", product: " C", imageUrl: "https://via.placeholder.com/150" },
        { name: " D", alumni: " D", description: " D", product: " D", imageUrl: "https://via.placeholder.com/150" },
        { name: " E", alumni: " E", description: " E", product: " E", imageUrl: "https://via.placeholder.com/150" },
        { name: " F", alumni: " F", description: " F", product: " F", imageUrl: "https://via.placeholder.com/150" },
        { name: " G", alumni: " G", description: " G", product: " G", imageUrl: "https://via.placeholder.com/150" },
        { name: " H", alumni: " H", description: " H", product: " H", imageUrl: "https://via.placeholder.com/150" },
        { name: " I", alumni: " I", description: " I", product: " I", imageUrl: "https://via.placeholder.com/150" },
        { name: " J", alumni: " J", description: " J", product: " J", imageUrl: "https://via.placeholder.com/150" }
    ];

    const [chunkSize, setChunkSize] = useState(4); // 4

    // 
    const updateChunkSize = () => {
        if (window.innerWidth < 768) {
            setChunkSize(1); // 1
        } else {
            setChunkSize(4); // 4
        }
    };

    // 
    useEffect(() => {
        updateChunkSize(); // 
        window.addEventListener("resize", updateChunkSize); // 

        // 
        return () => {
            window.removeEventListener("resize", updateChunkSize);
        };
    }, []);

    //  chunkSize 
    const companyChunks = [];
    for (let i = 0; i < companies.length; i += chunkSize) {
        companyChunks.push(companies.slice(i, i + chunkSize));
    }

    return (
        <Container className="carousel-container">
            <Carousel interval={null} prevIcon={<span className="carousel-control-prev-icon custom-prev-icon" />} nextIcon={<span className="carousel-control-next-icon custom-next-icon" />}>
                {companyChunks.map((chunk, idx) => (
                    <Carousel.Item key={idx}>
                        <div className="d-flex justify-content-around flex-wrap">
                            {chunk.map((company, idx) => (
                                <CompanyCard key={idx} company={company} />
                            ))}
                        </div>
                    </Carousel.Item>
                ))}
            </Carousel>
            <p className="text-center mt-3 slide-text"></p>
        </Container>
    );
};

export default CompanyList;
