import React, { useState } from "react";
import { Modal, Button, Form } from "react-bootstrap";

const ActionModal = ({ actionType, photo, onClose, onDelete, onEdit, onToggleStatus }) => {
  const [title, setTitle] = useState(photo.title);
  const [description, setDescription] = useState(photo.description);

  const handleSave = () => {
    if (actionType === "edit") {
      onEdit(photo.id, { title, description });
    } else if (actionType === "toggleStatus") {
      onToggleStatus(photo.id);
    } else if (actionType === "delete") {
      onDelete(photo.id);
    }
    onClose();
  };

  const getModalTitle = () => {
    switch (actionType) {
      case "edit":
        return "";
      case "delete":
        return "";
      case "toggleStatus":
        return photo.is_active ? "" : "";
      default:
        return "";
    }
  };

  return (
    <Modal show onHide={onClose}>
      <Modal.Header closeButton>
        <Modal.Title>{getModalTitle()}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {actionType === "edit" ? (
          <Form>
            <Form.Group controlId="formTitle">
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
              />
            </Form.Group>
            <Form.Group controlId="formDescription">
              <Form.Label></Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                value={description}
                onChange={(e) => setDescription(e.target.value)}
              />
            </Form.Group>
          </Form>
        ) : (
          <p>{actionType === "delete" ? "" : photo.is_active ? "" : ""}</p>
        )}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={onClose}>
          
        </Button>
        <Button variant={actionType === "delete" ? "danger" : "primary"} onClick={handleSave}>
          
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ActionModal;
